* {
  box-sizing: border-box;
  margin:0;
  padding:0;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}



.twitter {
  padding-top: 80px;
  align-self: center;
}

.twitter h4 {
  margin: 30px 0;
}

nav {
  height: 60px;
  width: 100%;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  background-color: #282c34;
  box-shadow: 0 1px 5px #00000066;
}

.sidebar {
  height: 100vh;
  background: #ffb142;
  width:170px;
  transition: 2ms ease-in-out;
  color: white;
  z-index: 4;
  position: absolute;
  top: 0;
  left: 0;
}

.sidebar li {
  list-style: none;
  padding: 40px 0;
}

.sidebar a {
  text-decoration: none;
  color: #282c34;
  font-size: 1.5rem;
}

.sidebar a:hover {
  color: #cc8e35;
}

nav h2 {
  color: #ffb142;
  margin: 0 auto;
  align-self: center;
}

nav .hamburger {
  width: 80px;
  height: 80px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  cursor: pointer;
}

nav .stick {
  width: 40px;
  height: 5px;
  border-radius: 4px;
  margin-bottom: 6px;
  background-color: #ffb142;
  display: inline-block;
}

nav .stick:last-child {
  margin-bottom: 0;
}

