.home {
  padding-top:80px;
}

.home ul {
  list-style: none;
  padding-top: 30px;
}

.home li {
  padding: 30px 0;
}

.home a {
  color: #ffb142;
  text-decoration: none;
  font-size:2rem;
  display:flex;
  border-radius:15px;
  height: 100%;
  width:100%;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.home .flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home .card {
  width:300px;
  height: 150px;
  padding: 5px 0;
  transition: 0.3s;
}

.home .card:hover {
  transform: scale(1.1);
}

.home .card .tt {
  background: url("images/twitter.png") no-repeat ;
  background-size: cover;
}

.home .card .podkarpacie {
  background: url("images/podkarpacie.png") no-repeat;
  background-size: cover;
}

.home .card .ruletka {
  background: url("images/ruletka.png") no-repeat;
  background-size: cover;
}

.home .card img {
  height: 100%;
  width: 100%;
  border-radius:15px;
}

.roulette {
  box-sizing: border-box;
  align-self: center;
  width: 100%;
  margin: 0 auto;
  padding: 50px 15px 0;

}

.roulette h1 {

}

.roulette input {
  font-size: 20px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  width: 50%;
}


.roulette input:before {
  content: 'min/max';
  position: relative;
  display:block;
}

.roulette button {
  padding: 10px 50px;
  background: linear-gradient(0deg,#cc8e35  0%,#ffb142  100% );
  border-radius:10px;
  outline: none;
  border-color: transparent;
  display: block;
  margin: 20px auto;
  width: 100%;
  color: white;
  font-size:30px;
}

@media (min-width: 760px) {
  .twitter {
   width:100%;
  }
  .roulette {
    width:50%;
    padding: 200px 15px 0;
  }
  .sidebar {
    width:300px;
  }

}